@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;1,100;1,400&display=swap);
body
{
    margin: 0;
    background-color: #222831;
}
h1
{
    font-family: 'Roboto',sans-serif;
    color: #00ADB5;
}
p
{
    font-family: 'Roboto',sans-serif;
    color: #EEEEEE;
}
.searchbar
{
    width:75%;
    margin:auto;
    
  
}
.searchdiv
{
    width: 70%;
    margin: auto;
    
    
}
.search
{
    width: 90%;
}
.moviecard
{
   width: 90%;
   margin: auto;
   display: flex;
    flex-wrap: wrap;
   margin-top: 3%;
   background-color: #393E46;
   
   
}

.pos
{
    max-width: 100%;
    
}

.moviepos
{
    display: flex;
    width: 100%;
}
.title
{
    margin-left: 13px;
    
}
.titlediv
{
    width: 100%;
    margin-left: 5%;
}

@media only screen and (max-width: 992px)
{
    .moviecard
    {
   
        display: flex;
        justify-content: space-around;
   
    }
    .pos{
        max-width: 50%;
    }
}

